import './Posts-list.css';
import { Card, Timeline,  Button, Modal, Input, Select, Popconfirm, message } from 'antd';
import api from '../../utils/Api';
import React, { useEffect, useState } from "react";



function PostsList(props) {
  const [posts, setPosts] = useState([]);
  const [user, setUser] = useState({});
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [form, setFormValue] = useState({});
  const [editedPostId, setId] = useState('');


  useEffect(() => {
    setPosts(props.posts);
  }, [props.posts]);  
  
  const showModal = (post) => {
    setIsModalVisible(true);
    setId(post._id);
    setFormValue({...form, title: post.title, tags: post.tags, text: post.text, image: post.image}) 
  };
  const closeModal = () => {
    setIsModalVisible(!isModalVisible);
    setFormValue({});
    setId('');
  } 
  const inputChange = (value) => {
    setFormValue({...form, [value.target.name]: value.target.value});
  }
  const handleChange = (value) => {
    setFormValue({...form, tags: value});
  }
  function cancel(e) {
    console.log(e);
    message.error('Click on No');
  }
  useEffect(() => {
    Promise.all([api.getAllProducts(), api.getUserInfo()]).then(
      ([posts, user]) => {
        setPosts(posts.reverse());
        setUser(user);
      }
    );
  }, []);
//likes
  const likeFunction = (likes, postId) => {
      const alredyLiked = !!likes.find(id => id === user._id);

      if (!alredyLiked) {
        api.addLike(postId).then(() => 
          api.getAllProducts().then(posts => setPosts(posts.reverse()))
        )
        return;
      }
      api.removeLike(postId).then(() =>
        api.getAllProducts().then(posts => setPosts(posts.reverse())))
  };
  const active = (likes) => {
    return !!likes.find(id => id === user._id)? ' _active' : '';
  }
//delete post
  const deletePost = (postId) => {
    api.deletePost(postId).then(() =>
      api.getAllProducts().then(posts => setPosts(posts.reverse()))
    )
    setIsModalVisible(false)
  }
//update post
  const updatePost = () => {
    if (form.text && form.title) {
      api.updatePost({...form}, editedPostId).then(closeModal).then(() => 
            api.getAllProducts().then(posts => setPosts(posts.reverse())));
          return;
    }
    alert('Please fill in "title" and "text" :)')
  }
  // post list
  return (
    <div className="Posts-list"> {
        posts?.length && posts.map(post => {
        return(
          <Card className='post-card' key={post._id} 
          title={<a className='post-card__title' href='/'>{post.title}</a>}>
            <div className='avatar-mail'>
            {/* avatar */}
            {
            !post.author?.avatar || post.author?.avatar === "https://react-learning.ru/image-compressed/default-image.jpg" ?
            <img className='post-avatar' src='https://community.adobe.com/legacyfs/online/avatars/a829412_stormtrooper_001.png'  alt='img'/>
            :
            <img className='post-avatar' src={post.author?.avatar}  alt="avatar"/>
            }
            {/* name */}
              {
              !post.author?.name ?
              <p className='unknown-author'>author unknown</p>
              :
              <p>{post.author?.name}</p> 
              }
            </div>
            {/* text */}
            <div className='post-text'>
            <p>{post.text}</p>
            </div>
            <div className='post-photo'>
            {
            post.image === "https://react-learning.ru/image-compressed/default-image.jpg" ? 
            <img className='post-img' src='https://cdn.dribbble.com/users/1152726/screenshots/2940106/dribble.gif'  alt='img'/>
            :
            <img className='post-img' src={post?.image}  alt='img'/>
            }
            </div>
            {/* tags */}
            <div className='tags'>
              <div>Tags:</div>
              {
              post.tags.map(tag => {
              return(
              <div key={tag + Math.random()} className='tag'>{tag}</div>)
              })
              }
              </div>
              {/* time */}
            <Timeline>
              <Timeline.Item color="blue">Created at {new Date(post.created_at).toLocaleString()}</Timeline.Item>
              <Timeline.Item color="green">Updated at {new Date(post.updated_at).toLocaleString()}</Timeline.Item>
            </Timeline>
            <div className='card-footer'>
              <div className={'likes' + active(post.likes)} onClick={() => likeFunction(post.likes, post._id)}>LIKES: {post.likes.length}</div>
            {/* delete */}
            {
            post.author?._id === user._id ? 
              <Popconfirm
                title="Are you sure to delete?"
                onConfirm={() => deletePost(post._id)}
                onCancel={cancel}
                okText="Yes"
                cancelText="No">
                <a>
                <svg className='trash bi bi-trash' xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" viewBox="0 0 16 16">
                <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                <path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                </svg>      
                </a>
              </Popconfirm>
              : ''
              }
              {/* edit */}
              {
              post.author?._id === user._id ? 
              <Button type="edit" className='edit-button' onClick={() => showModal(post)}>             
              edit              
              </Button>
            : ''
            }
            </div>
          </Card>
        )})
      }
      {/* edit modal */}
      <Modal className='edit-button' title="Edit post" visible={isModalVisible} onOk={updatePost} onCancel={closeModal}>
      <Input className='input' value={form.title} name='title' onChange={inputChange}/>
      <Input className='input' value={form.text} name='text' onChange={inputChange}/>
      <Select className='input' value={form.tags} mode="tags" style={{ width: '100%' }} name='tags'  onChange={handleChange}>
      {[]}
      </Select>
      <Input className='input' value={form.image} name='image' onChange={inputChange}/>
      </Modal>
    </div>
  );
}

export default PostsList;

